import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { db } from "./firebase-config";

import {
  addDoc,
  collection,
  doc,
  getDoc,
  getDocs,
  deleteDoc,
  updateDoc
} from "firebase/firestore";

function index() {
  const [tasks, setTasks] = useState([]);
  const [input, setInput] = useState("");
  const [isEdit, setIsEdit] = useState(true);
  const [editId, setEditId] = useState("");

  const userCollectionRef = collection(db, "tasks");

  // ! task add
  const addTasks = async () => {
    // const newTask = {
    //   id: new Date().getTime().toString(),
    //   name: input,
    // };

    // setTasks((prev) => [...prev, newTask]);

    await addDoc(userCollectionRef, { name: input });
    setInput("");
    location.reload();
  };

  // ! delete task
  const deleteTask = async (id) => {
    // const newTask = tasks.filter((task) => task.id !== id);
    // setTasks(newTask);

    // const taskDoc = doc(db, "tasks", id);
    // await deleteTask(taskDoc);

    const userDoc = doc(db, "tasks", id);
    await deleteDoc(userDoc);
    location.reload();
  };

  // ! edit task
  const editTasks = (id, name) => {
    setIsEdit(!isEdit);
    setInput(name);
    setEditId(id);
  };

  // ! update task
  const updateTask = async () => {
    // const newTask = {
    //   id: new Date().getTime().toString(),
    //   name: input,
    // };

    const tasksDoc = doc(db, "tasks", editId);
    const newTask = { name: input };
    await updateDoc(tasksDoc, newTask);
    //deleteTask(editId);
    //setTasks((prev) => [...prev, newTask]);

    setIsEdit(!isEdit);
    setInput("");
    location.reload();
  };

  useEffect(() => {
    const getTask = async () => {
      const data = await getDocs(userCollectionRef);
      setTasks(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
      console.log(tasks);
    };
    getTask();
  }, []);

  return (
    <Wrepper>
      <div className="container">
        <div className="small-container">
          <div className="input__box">
            <input
              type="text"
              value={input}
              placeholder="Enter your task"
              required
              onChange={(e) => setInput(e.target.value)}
            />
            {isEdit ? (
              <button onClick={addTasks}>
                <i className="fa-solid fa-square-plus"></i>
              </button>
            ) : (
              <button onClick={updateTask}>
                <i className="fa-solid fa-pen-to-square"></i>
              </button>
            )}
          </div>

          <div className="task_List">
            {tasks.map((task, index) => (
              <div className="row" key={task.id}>
                <div>
                  <p>{task.name}</p>
                </div>

                <div className="buttons">
                  <button onClick={() => editTasks(task.id, task.name)}>
                    <i className="fa-solid fa-pen-to-square"></i>
                  </button>
                  <button onClick={() => deleteTask(task.id)}>
                    <i className="fa-solid fa-trash"></i>
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
      {/* <div className="box-1"></div>
      <div className="box-2"></div> */}
    </Wrepper>
  );
}

const Wrepper = styled.div`
  // background-color: #0093e9;
  //background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);

  background: linear-gradient(90deg, #1cb5e0 0%, #000851 100%);

  width: 100%;
  height: 100vh;
  padding: 40px;

  display: flex;
  justify-content: center;

  button {
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
  }

  .container {
    width: 500px;
    height: 90vh;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    z-index: 100;
    position: relative;

    .small-container {
      width: 80%;
      margin: 0 auto;
    }
    .input__box {
      width: 100%;
      margin: 0 auto;
      padding: 5px;
      margin-top: 20px;
      background: #fff;

      border-radius: 4px;
      position: relative;
      input {
        width: 100%;
        padding: 5px;
        background: transparent;
        border: none;
        outline: none;
        font-size: 18px;
      }
      button {
        position: absolute;
        top: 15%;
        right: 10px;
        color: #80d;
        i {
          font-size: 28px;
        }
      }
    }
    .task_List {
      margin-top: 40px;
      background: #fafafa;

      //background: linear-gradient(90deg, #d53369 0%, #daae51 100%);

      padding: 10px;
      height: 68vh;
      border-radius: 4px;
      //color: #fff;
      .row {
        display: flex;

        justify-content: space-between;
        background: #1cb5e0;
        padding: 5px;
        margin-bottom: 15px;

        background: rgb(255, 255, 255);
        background: linear-gradient(
          184deg,
          rgba(255, 255, 255, 0.8580474426098564) 0%,
          rgba(232, 232, 232, 1) 100%
        );
      }
      p {
        font-size: 22px;
      }
      .buttons i {
        margin-left: 15px;
        font-size: 22px;
        color: #ff0283;
      }
    }
  }

  .box-1 {
    background: linear-gradient(90deg, #d53369 0%, #daae51 100%);
    width: 200px;
    height: 100px;
    border-radius: 6px;

    position: absolute;
    top: 20%;
    left: 30%;
    transform: rotate(-190deg);
  }
  .box-2 {
    background: linear-gradient(90deg, #00c9ff 0%, #92fe9d 100%);
    width: 200px;
    height: 100px;
    border-radius: 6px;

    position: absolute;
    top: 40%;
    left: 52%;
    transform: rotate(190deg);
  }

  @media screen and (max-width: 600px) {
    .container {
      width: 500px;
      height: 90vh;

      border-radius: 10px;

      .input__box {
        input {
          font-size: 18px;
        }
        button {
          i {
            font-size: 28px;
          }
        }
      }
      .task_List {
        p {
          font-size: 16px;
        }
        .buttons i {
          font-size: 18px;
        }
      }
    }
  }
`;

export default index;
